	module.exports = {"data":{"site":{"siteMetadata":{"title":"weiran.co","author":"Weiran Zhang"}},"markdownRemark":{"id":"/Users/weiran/git/weiran/weiran.co/src/pages/blog/2013/2/why-did-dell-buy-dell/index.md absPath of file >>> MarkdownRemark","html":"<blockquote>\n<p>This is Dell’s Battle of Britain. This could be his finest hour or it could be him and his ego, like Butch Cassidy and the Sundance Kid’s last stand against the Bolivian army. Who knows.</p>\n<p>So why do it? My explanation — you don’t really have a rational choice, so you make an emotional choice. As a founder whose name adorns the shingle outside the door, Dell is playing a high stakes poker to literally save his good name. Just the idea that some faceless Asian company would own “Dell” the brand and his name is anathema.</p>\n</blockquote>\n<p>Michael Dell is going all-in, good luck to him. Hopefully he’ll come with something more than just a commodity PC manufacturer at the other side.</p>","frontmatter":{"title":"Why did Dell buy Dell","date":"February 06, 2013","passthroughUrl":"http://gigaom.com/2013/02/05/welcome-back-michael-dell-its-time-to-reclaim-your-name/"}}},"pathContext":{"slug":"/blog/2013/2/why-did-dell-buy-dell/","previous":{"fields":{"slug":"/blog/2013/2/civilized-discourse-construction-kit/"},"frontmatter":{"title":"Civilized Discourse Construction Kit","passthroughUrl":"http://www.codinghorror.com/blog/2013/02/civilized-discourse-construction-kit.html"}},"next":{"fields":{"slug":"/blog/2013/2/whos-being-dishonest-with-storage-space/"},"frontmatter":{"title":"Who's being dishonest with storage space?","passthroughUrl":"http://www.zdnet.com/surface-pro-versus-macbook-air-whos-being-dishonest-with-storage-space-7000011009/"}}}}

